










































import Vue from 'vue'
import NavLinks from '@/components/NavLinks.vue'

export default Vue.extend({
    data(): {
        showSidebar: boolean
    } {
        return {
            showSidebar: false,
        }
    },

    created() {
        // @ts-ignore
        Event.$on('new-view', () => this.showSidebar = false)
    },

    methods: {
        toggleSidenav(): void {
            this.showSidebar = !this.showSidebar
        }
    },

    components: {
        NavLinks,
    },
})
